import { Component, OnInit, HostBinding, ViewChild, TemplateRef } from '@angular/core';
import { GalleryComponent, Gallery, GalleryItem ,GalleryRef } from '@ngx-gallery/core';
import { Lightbox } from '@ngx-gallery/lightbox';
import { ActivatedRoute } from '@angular/router';
import { HostListener } from "@angular/core";

import { Pots } from '../pots'
import { Pot } from '../pot'

import { CartService } from '../../cart.service';
import { IMAGEVIEWER_CONFIG, ImageViewerConfig, createButtonConfig } from '@hallysonh/ngx-imageviewer';

const MY_IMAGEVIEWER_CONFIG: ImageViewerConfig = {
  bgStyle: 'transparent', // custom container's background style
  loadingMessage: 'loading',//<i class="fa fa-spinner fa-pulse fa-3x fa-fw"></i>
  rotateRightButton: createButtonConfig('','',4,false),
  rotateLeftButton: createButtonConfig('','',4,false),
};

@Component({
  selector: 'app-pot-detail',
  templateUrl: './pot-detail.component.html',
  styleUrls: ['./pot-detail.component.css'],
  providers: [
    {
      provide: IMAGEVIEWER_CONFIG,
      useValue: MY_IMAGEVIEWER_CONFIG,
    }
  ],
})

export class PotDetailComponent implements OnInit {
  screenHeight: number;
  screenWidth: number;

  galleryId ='gal';
  name: string;
  pots: Pot[];
  items: GalleryItem[];
  galRef: GalleryRef;
  lightbox: Lightbox;

  @ViewChild('itemTemplate', {static: true}) itemTemplate: TemplateRef<any>;

  constructor(
    gallery: Gallery,
    private route: ActivatedRoute,
    private cartService: CartService,
  ) {
    this.galRef = gallery.ref(this.galleryId);
  }

  ngOnInit() {
    this.getScreenSize();

    this.galRef.setConfig({
      thumb: false,
      loadingMode: 'indeterminate',
      itemTemplate: this.itemTemplate,
      gestures: false,
      //imageSize: 'cover',
      loadingIcon: 'Loading...'
    });

    this.route.data.subscribe((data: { pots: Pots }) => {
      this.pots = data.pots.pots;
      this.name = data.pots.name;
      this.items = this.pots.map(p => { //format of galleryItem
        return{
          type:'image',
          data:{
            src: p.src,
            thumb: p.src,
            title: p.name,
            description: p.description,
            date: p.date,
            type: p.type,
            price: p.price,
            status: p.status,
          }
        };
      });
      this.galRef.load(this.items);
    });
  }

  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
    this.screenHeight = window.innerHeight;
    this.screenWidth = window.innerWidth;
  }

  getTypeIcon( t ) {
    switch (t) {
      case "Bowl":
        return "url(/assets/icons/pot_bowl.ico)";
      case "Cup":
        return "url(/assets/icons/pot_cup.ico)";
      case "Flower Pot":
        return "url(/assets/icons/pot_flowpot.ico)";
      case "Plate":
        return "url(/assets/icons/pot_plate.ico)";
      case "Vase":
        return "url(/assets/icons/pot_vase.ico)";
      case "Decoration":
        return "url(/assets/icons/pot_deco.ico)";
    }
    return "url(/assets/icons/pot_other.ico)";

  }

  share() {
    window.alert('The product has been shared!');
    ///TODO: share code
  }
  addToCart(pottery) {
    window.alert('Your ' + pottery.name + ' has been added to the cart!');
    this.cartService.addToCart(pottery);
  }
  available(pottery) {
    if (this.cartService.notInCart(pottery) < 0 && pottery.status == 1) {
      return 1;
    }
    else {
      return 0;
    }
  }

}
