import { BehaviorSubject, Observable, of, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import {  HttpClient, HttpParams ,HttpErrorResponse }  from  '@angular/common/http';

import { Injectable } from '@angular/core';
import { Pots } from './pots';

@Injectable({
  providedIn: 'root',
})
export class PotsService {

  private kilns: Array<Pots> ;
  private TABLE_URL = "https://zyha.tk/a/get-table.php";

  constructor(
    private httpClient:  HttpClient,
  ) { }

  getKiln():Observable<Pots[]>{
    return this.httpClient.get(this.TABLE_URL, {
      params: new HttpParams().set('table', 'pots')}).pipe(
        map((res) => {
          console.log(res);

          this.kilns = res['data'];
          console.log(this.kilns);
          return this.kilns;

        }),
        catchError(this.handleError));
  }
  private handleError(error: HttpErrorResponse) {
    console.log(error);
    // return an observable with a user friendly message
    return throwError('Error! something went wrong.');
  }
  getPots(id: string ) {
    return this.getKiln().pipe(
      map(pots => pots.find(pots => pots.name === id))
    );
  }

  //addCrisis(name: string) {
//    name = name.trim();
  //  if (name) {
  //    let crisis = { id: CrisisService.nextCrisisId++, name };
  //    CRISES.push(crisis);
  //    this.crises$.next(CRISES);
  //  }
  //}
}


