import { Component, OnInit, HostBinding} from '@angular/core';
import { NavbarService } from '../../navbar.service';
import { ActivatedRoute, Router } from '@angular/router';
import { HostListener } from "@angular/core";
import { Observable }     from 'rxjs';
import { switchMap }      from 'rxjs/operators';

import { Pot } from '../pot';
import { Pots }         from '../pots';
import { PotsService }  from '../pottery.service';


@Component({
  selector: 'app-pottery',
  templateUrl: './pottery.component.html',
  styleUrls: ['./pottery.component.css']
})

export class PotteryComponent implements OnInit {
  screenHeight: number;
  screenWidth: number;
  kiln$: Observable<Pots[]>;

  constructor(
    public nav: NavbarService,
    private service: PotsService,
    private route: ActivatedRoute,
    public router: Router,
  ) {
    this.getScreenSize();
  }

  ngOnInit() {

    this.kiln$ = this.route.paramMap.pipe(
      switchMap(params => {
        return this.service.getKiln();
      })
    );
    this.nav.show();
    this.nav.setDeg(270);
  }

  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
    this.screenHeight = window.innerHeight;
    this.screenWidth = window.innerWidth;
  }
}
